.progress-indicator {
  position: relative; }
  .progress-indicator.donut {
    overflow: hidden;
    width: 100%;
    padding-bottom: 100%;
    border-radius: 50%;
    background-color: #f2f2f2; }
    .progress-indicator.donut .donut-left-slice, .progress-indicator.donut .donut-right-slice {
      position: absolute;
      top: 0;
      width: 50%;
      height: 100%;
      transition: transform 0.6s ease-in-out; }
    .progress-indicator.donut .donut-left-slice {
      left: 0;
      border-bottom-left-radius: 1000px;
      border-top-left-radius: 1000px; }
    .progress-indicator.donut .donut-right-slice {
      right: 0;
      border-bottom-right-radius: 1000px;
      border-top-right-radius: 1000px; }
    .progress-indicator.donut .transition-base {
      transition: transform 0.3s linear; }
    .progress-indicator.donut .reset-slice {
      transform: rotate(0deg);
      transition: none;
      background: inherit; }
    .progress-indicator.donut.rotate-anticlockwise .donut-left-slice {
      z-index: 2;
      transform-origin: center right; }
    .progress-indicator.donut.rotate-anticlockwise .donut-right-slice {
      z-index: 1;
      transform-origin: center left; }
  .progress-indicator.indicator-color-danger-bright.donut {
    background-color: #fdecec; }
  .progress-indicator-donut-overlay {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 92%;
    height: 92%;
    z-index: 5;
    border-radius: 50%;
    background: #FFFFFF; }
    .progress-indicator-donut-overlay .label-amount-left {
      margin: 0; }
  .progress-indicator .d-table {
    display: table;
    width: 100%;
    height: 100%; }
  .progress-indicator .d-table-cell {
    width: 100%;
    height: 100%;
    vertical-align: middle;
    text-align: center; }

.label-color-success {
  color: #46c1a6; }

.indicator-color-success {
  background-color: #46c1a6; }

.label-color-warning {
  color: #cc8818; }

.indicator-color-warning {
  background-color: #FFAA1E; }

.label-color-danger {
  color: #ED3E44; }

.indicator-color-danger {
  background-color: #ED3E44; }

/**
 * --------------------------------------------------------------------------
 * UI BB Search Box
 * --------------------------------------------------------------------------
 * @name ui-bb-search-box-ng
 * @description
 * UI component to create search box.
 *
 * @example
 * // Search box with value
 * <ui-bb-search-box-ng>
 *   <div class="bb-search-box">
 *     <label class="hidden">Search</label>
 *     <i class="fa fa-search bb-search-box-icon" aria-hidden="true"></i>
 *     <input type="search" class="form-control bb-search-box-input">
 *     <button type="button" class="close bb-search-box-clear" aria-label="Clear">
 *       <span aria-hidden="true">&times;</span>
 *     </button>
 *   </div>
 * </ui-bb-search-box-ng>
 *
 * // Empty search box
 * <ui-bb-search-box-ng>
 *   <div class="bb-search-box empty">
 *     <label class="hidden">Search</label>
 *     <i class="fa fa-search bb-search-box-icon" aria-hidden="true"></i>
 *     <input type="search" class="form-control bb-search-box-input">
 *     <button type="button" class="close bb-search-box-clear" aria-label="Clear">
 *       <span aria-hidden="true">&times;</span>
 *     </button>
 *   </div>
 * </ui-bb-search-box-ng>
 *
 * // Disabled search box
 * <ui-bb-search-box-ng>
 *   <div class="bb-search-box disabled">
 *     <label class="hidden">Search</label>
 *     <i class="fa fa-search bb-search-box-icon" aria-hidden="true"></i>
 *     <input type="search" class="form-control bb-search-box-input">
 *     <button type="button" class="close bb-search-box-clear" aria-label="Clear">
 *       <span aria-hidden="true">&times;</span>
 *     </button>
 *   </div>
 * </ui-bb-search-box-ng>
 */
.bb-search-box {
  display: block;
  position: relative; }
  .bb-search-box .close {
    font-size: 16px;
    color: #333333;
    opacity: 0.5;
    filter: alpha(opacity=50); }
    .bb-search-box .close:hover, .bb-search-box .close:focus {
      opacity: 0.6;
      filter: alpha(opacity=60); }

.bb-search-box-wrapper {
  width: 100%; }

.bb-search-box-input {
  padding-right: 30px;
  border: 1px solid #cccccc;
  border-radius: 2px;
  height: 34px; }

.bb-search-box-icon,
.bb-search-box-clear,
.bb-search-box-spinner {
  position: absolute;
  top: 50%;
  z-index: 5;
  width: 1.8rem;
  height: 1.8rem;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  transform: translate(0, -50%); }

.bb-search-box-clear,
.bb-search-box-spinner {
  right: 50px; }
  .bb-search-box-clear.shift-right,
  .bb-search-box-spinner.shift-right {
    right: 10px; }

.bb-search-box-icon {
  left: 10px;
  color: #999999; }

.bb-search-box-spinner {
  width: 1.8rem;
  height: 1.8rem;
  background: url(../assets/images/spinner.svg) no-repeat;
  background-size: 1.8rem; }

