.dropdown-select {
  .dropdown-selected {
    color: $text-color;
  }
}

.bb-account-selector {
  .dropdown-menu {
    padding: 0;
    border: none;
    border-radius: $border-radius-medium;
    max-height: none;
    overflow: hidden;

    &:hover {
      cursor: pointer;
    }

    .dropdown-option {
      &:not(:last-child) {
        margin-bottom: $account-selector-options-margin-bottom;
        border-top: $default-border;
        border-bottom: $default-border;
      }

      &:hover {
        cursor: pointer;
      }

      &[disabled] {
        cursor: not-allowed;
      }
    }
  }
}

.bb-account-selector-toggle {
  height: auto;

  &.drop-shadow {
    @include box-shadow($default-box-shadow);
  }

  &.dropdown-toggle {
    padding: 0;
    border: none;
    border-radius: $border-radius-medium;
    background-color: $white;

    .dropdown-option {
      margin: 0;

      &.placeholder {
        @extend .px-3;
        @extend .py-4;
        @extend .double-line;
      }
    }

    .chevron-icon {
      right: $account-selector-chevron-position-right;
      color: $gray-dark;
      pointer-events: none;
    }

    &:focus .placeholder {
      background-color: $dropdown-link-hover-bg;
    }
  }
}

.bb-account-selector-option {
  color: $text-color;
  position: relative;

  .dropdown-toggle & {
    min-height: $account-selector-height;
  }

  &:hover,
  &.active,
  &:active,
  &.focus,
  &:focus {
    color: $text-color;
    background-color: $dropdown-link-hover-bg;
    text-decoration: none;
  }

  &:hover input[type="checkbox"]::before {
    border-color: $brand-primary;
  }

  &[disabled] {
    color: $gray;
    cursor: not-allowed;

    &:hover,
    &.active,
    &:active,
    &.focus,
    &:focus {
      color: $gray;
      background-color: transparent;
    }
  
    &:hover input[type="checkbox"]::before {
      border-color: $gray;
    }

    .amount-positive {
      color: $brand-primary-lighter;
    }

    .amount-negative {
      color: $brand-danger-lighter;
    }
  }
}

.bb-account-selector-option-content {
  min-height: $account-selector-option-content-height;
}

.bb-account-selector-scrollable-area {
  max-height: $account-selector-scrollbar-height;
}

.bb-account-selector-spinner {
  @include spinner($account-selector-spinner-size);
}



